%vertical-rhythm {
    margin-bottom: $spacing-unit / 2;
}

html {
  scroll-behavior: smooth;
}

.box {
  box-sizing: border-box; 
}

/* Grid
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.container {
  position:relative;
  width: 100%;
  max-width: 1280px;

  margin: 0 auto;
  padding: 10px 10px;
  box-sizing: border-box;

  border-left: 1px dashed #434343;
  border-right: 1px dashed #434343;
  background-color: #1C1C1C;

}

.column,
.columns {
  width: 100%;
  float: right;
  box-sizing: border-box; }

/* For devices larger than 400px */
@media (min-width: 534px) {
  .container {
    width: 100%;
    padding: 32px 32px; }
}

/* For devices larger than 780px */
@media (min-width: 781px) {
  .container {
    width: 90%; }
  .column,
  .columns {
    margin-right: 4%; }
  .column:first-child,
  .columns:first-child {
    margin-right: 0; }

  .one.column,
  .one.columns                    { width: 4.66666666667%; }
  .two.columns                    { width: 13.3333333333%; }
  .three.columns                  { width: 22%;            }
  .four.columns                   { width: 30.6666666667%; }
  .five.columns                   { width: 39.3333333333%; }
  .six.columns                    { width: 48%;            }
  .seven.columns                  { width: 56.6666666667%; }
  .eight.columns                  { width: 65.3333333333%; }
  .nine.columns                   { width: 74.0%;          }
  .ten.columns                    { width: 82.6666666667%; }
  .eleven.columns                 { width: 91.3333333333%; }
  .twelve.columns                 { width: 100%; margin-right: 0; }

  .one-third.column               { width: 30.6666666667%; }
  .two-thirds.column              { width: 65.3333333333%; }

  .one-half.column                { width: 48%; }

  /* Offsets */
  .offset-by-one.column,
  .offset-by-one.columns          { margin-right: 8.66666666667%; }
  .offset-by-two.column,
  .offset-by-two.columns          { margin-right: 17.3333333333%; }
  .offset-by-three.column,
  .offset-by-three.columns        { margin-right: 26%;            }
  .offset-by-four.column,
  .offset-by-four.columns         { margin-right: 34.6666666667%; }
  .offset-by-five.column,
  .offset-by-five.columns         { margin-right: 43.3333333333%; }
  .offset-by-six.column,
  .offset-by-six.columns          { margin-right: 52%;            }
  .offset-by-seven.column,
  .offset-by-seven.columns        { margin-right: 60.6666666667%; }
  .offset-by-eight.column,
  .offset-by-eight.columns        { margin-right: 69.3333333333%; }
  .offset-by-nine.column,
  .offset-by-nine.columns         { margin-right: 78.0%;          }
  .offset-by-ten.column,
  .offset-by-ten.columns          { margin-right: 86.6666666667%; }
  .offset-by-eleven.column,
  .offset-by-eleven.columns       { margin-right: 95.3333333333%; }

  .offset-by-one-third.column,
  .offset-by-one-third.columns    { margin-right: 34.6666666667%; }
  .offset-by-two-thirds.column,
  .offset-by-two-thirds.columns   { margin-right: 69.3333333333%; }

  .offset-by-one-half.column,
  .offset-by-one-half.columns     { margin-right: 52%; }

}


/* Base Styles
–––––––––––––
––––––––––––––––––––––––––––––––––––– */
/* NOTE
html is set to 62.5% so that all the REM measurements throughout Skeleton
are based on 10px sizing. So basically 1.5rem = 15px :) */
html {
  font-size: 62.5%; }
body {
  font-size: 1.6em; /* currently ems cause chrome bug misinterpreting rems on body element */
  line-height: 1.6;
  font-weight: 400;
  /* font-family: Helvetica, Tahoma, Arial, STXihei, "华文细黑", "Microsoft YaHei", "微软雅黑", SimSun, "宋体", Heiti, "黑体", sans-serif; */
  /* font-family: "Raleway", "HelveticaNeue", "Helvetica Neue", Helvetica, Arial, sans-serif; */
  font-family: "Helvetica Neue","Noto Sans TC","PingFang TC","Hiragino Sans GB","Microsoft YaHei",sans-serif;
  color: #ccc; }


/* Typography
–––––––––––––––––––––––––––––––––––––––––––––––––– */
h1, h2, h3, h4, h5, h6 {
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-weight: 400; 
  color: #FFF;
  font-family: 'Ubuntu','Lucida Grande','Noto Sans TC',sans-serif;
  }
h1 { font-size: 2.2rem; line-height: 1.2;  letter-spacing: 0rem;}
h2 { font-size: 2.0rem; line-height: 1.25; letter-spacing: 0rem; font-weight: bold;}
h3 { font-size: 1.7rem; line-height: 1.3;  letter-spacing: -.1rem; }
h4 { font-size: 1.5rem; line-height: 1.35; letter-spacing: -.08rem; }
h5 { font-size: 1.3rem; line-height: 1.5;  letter-spacing: -.05rem; }
h6 { font-size: 1.1rem; line-height: 1.6;  letter-spacing: 0; }

/* Larger than phablet */
@media (min-width: 550px) {
  h1 { font-size: 2.4rem; }
  h2 { font-size: 2.2rem; }
  h3 { font-size: 2.0rem; }
  h4 { font-size: 1.7rem; }
  h5 { font-size: 1.4rem; }
  h6 { font-size: 1.1rem; }
}

p {
  margin-top: 0; }


/* Links
–––––––––––––––––––––––––––––––––––––––––––––––––– */
a {
  color: $pri-color;
  text-decoration: none;
  //background-color: transparent;
  -webkit-transition: color .4s;
  transition: color .4s;
}

/*a:visited{
  color: $pri-color-dark;
}
*/
a:hover {
  color: $sec-color;
  text-decoration: underline;
}


